/*========== Basic ==========*/
html {
  font-size: 16px; }

body {
  font-family: 'Golos', sans-serif;
  text-rendering: geometricPrecision;
  color: var(--color-text, #333333);
  font-size: 0.875rem;
  line-height: 1.25rem;
  min-width: 320px; }

* {
  box-sizing: border-box;
  min-height: 0;
  min-width: 0;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  outline: none; }
  *:before, *:after {
    box-sizing: border-box; }

button, input, textarea {
  outline: none;
  display: block;
  background-color: transparent;
  box-shadow: none;
  border: 0;
  appearance: none;
  padding: 0;
  border-radius: 0; }

a, button, label {
  user-select: none;
  -webkit-user-drag: none;
  cursor: pointer; }

table {
  border-collapse: collapse; }

.background {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  pointer-events: none;
  user-select: none;
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  opacity: 0;
  visibility: hidden;
  transition: opacity 1s ease-out, visibility 1s ease-out; }
  .background.lazyloaded {
    opacity: 1;
    visibility: visible; }
  .background_left {
    background-position-x: left; }
  .background_right {
    background-position-x: right; }
  .background_top {
    background-position-y: top; }
  .background_bottom {
    background-position-y: bottom; }
  .background_contain {
    background-size: contain; }

.image {
  display: block;
  position: relative; }
  .image img {
    display: block;
    opacity: 0;
    max-width: 100%;
    visibility: hidden;
    transition: 1s ease-out; }
    .image img.lazyloaded {
      opacity: 1;
      visibility: visible; }
  .image_cover img, .image_contain img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%; }
  .image_cover {
